import * as Yup from 'yup';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import { useSnackbar } from 'notistack';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Alert, Stack} from '@mui/material';
import axios from '../../utils/axios';
import { FormProvider,   RHFTextField, RHFSelect } from '../../components/hook-form';

// drop dwon style
import useIsMountedRef from '../../hooks/useIsMountedRef';
// ----------------------------------------------------------------------

export default function System() {
    const { enqueueSnackbar } = useSnackbar();
    const isMountedRef = useIsMountedRef();
  const NewUserSchema = Yup.object().shape({
app_name: Yup.string().required(),
app_version: Yup.string().required(),
app_link: Yup.string().required(),
platform: Yup.string().required(),
 });
const app = ['ANDROID','IOS','SYSTEM'];
 
  const AccessToken =  window.localStorage.getItem('accessToken');
  const methods = useForm({
    resolver: yupResolver(NewUserSchema),
  });
  const {
    reset,
    setError,
    handleSubmit,
    formState: {errors, isSubmitting },
  } = methods;
 
  const onSubmit = async (data) => {
    try {
     await axios.post(`/api/new/app/${AccessToken}/habukhan/secure`,data)
     enqueueSnackbar('Inserted!');
     reset()
    } catch (error) {
        if (isMountedRef.current){
            setError('afterSubmit', error);
          }
    }
  };
   
  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>
     
        <Grid item xs={12} md={8}>
        {!!errors.afterSubmit && <Alert severity="error">{errors.afterSubmit.message}</Alert>}
          <Card sx={{ p: 3 }}>
     
            <Box
              sx={{
                display: 'grid',
                columnGap: 3,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(1, 1fr)' },
              }}
            >              
                 <RHFTextField name="app_name" label="APP NAME"/>
                 <RHFTextField name="app_version" label="APP VERSION"/>
                 <RHFTextField name="app_link" label="APP DOWNLOAD LINK" />
                 <RHFSelect name="platform" label="APP PLATFORM">
                <option value="" />
                {app.map((option) => (
                  <option key={option} value={option}>
                    {option}
                  </option>
                ))}
              </RHFSelect>
             </Box>
            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton  fullWidth size="large" type="submit" variant="contained" loading={isSubmitting}>
               Proceed
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
